@import '/src/app/styles/global';

.orders-list {
  row-gap: 32px;

  &__header {
    @include flex-container(row, space-between);
    padding: 10px 20px;
  }

  &__list {
    @include reset-list;
    @include flex-column;
    row-gap: 20px;
  }

  &__label {
    @include text-L-regular-styles;
    color: $grayColor;
  }
}
