@import '/src/app/styles/global';

.order-item {
  padding: 20px;
  height: 172px;

  &__header {
    @include flex-column;
    row-gap: 32px;
  }

  &__additional-info {
    @include flex-column;
    row-gap: 8px;
  }

  &__order-date {
    @include text-M-bold-styles;
  }

  &__order-number {
    @include text-M-main-styles($blueDarkColor);
  }

  &__company-info {
    @include flex-container(row, flex-start, center);
    column-gap: 8px;
  }

  &__company-name {
    @include text-M-bold-styles($grayColor);
  }

  &__company-logo {
    width: 40px;
    height: 40px;
  }


  &__delevery-info {

  }
}
